.transactionHistory {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
  
    width: 600px;
    border-radius: 4px;
  
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
  }
  .head {
    padding: 15px 0;
    /* padding-bottom: 15px; */
    background-color: cornflowerblue;
    text-transform: uppercase;
    color: cornsilk;
  }
  .type {
    width: calc(100% / 3);
    padding: 15px 0;
    padding-left: 55px;
    text-align: left;
    text-transform: capitalize;
    color: #757575;
  }
  .body {
    text-align: center;
    padding: 15px 0;
    color: #757575;
  }
  .row:nth-child(even) {
    background-color: #d3cdcd;
    color: #757575;
  }